.Game {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
    margin-top: 70px;
    .user-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 4px solid #3d3f3c;
        padding: 25px;
        border-radius: 20px;
    }
    .wins {
        border: 4px solid #4e9b3b;
        .picture {
            border: 4px solid #4e9b3b;
        }
    }
    .loses {
        border: 4px solid #b73619;
        .picture {
            border: 4px solid #b73619;
        }
    }
    .equally {
        border: 4px solid #3d3f3c;
        .picture {
            border: 4px solid #3d3f3c;
        }
    }
}
.User {
    display: flex;
    flex-direction: column;
    align-items: center;
    .picture {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        object-fit: cover;
    }
    .name {
        color: #3d3f3c;
    }
    .candys {
        color: #3d3f3c;
    }
}

.CountNavigation {
    display: flex;
    .button {
        width: 50px;
        height: 50px;
        margin: 10px;
        border: 2px solid #3d3f3c;
        border-radius: 10px;
        background: white;
        font-size: 25px;
        color: #3d3f3c;
        cursor: pointer;
    }
}
